import { useEffect } from 'react';
import { useColorMode } from '@chakra-ui/react';

import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min.js";

import './App.css';

import Navbar from './components/primary/js/Navbar.js';
import MainContent from './components/primary/js/MainContent.js';

function App() {

  const { toggleColorMode } = useColorMode();

  useEffect(() => {
    toggleColorMode();
  }, []);

  return (
    <div className="App">
      <Navbar />
      <MainContent />
    </div>
  );
}

export default App;
